body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/mev-portfolio/static/media/banner-bg.5f3ca497.png);
  background-size: cover;
  color: rgb(211, 213, 120);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.about-me {
    text-align: left;
    display: flex;
    margin: 10px 220px;
    grid-gap: 130px;
    gap: 130px;
}

/* .about-me-text {} */

.slide-in-from-left {
    transition: -webkit-transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
    -webkit-transform: translateX(-143%);
            transform: translateX(-143%);
}

.slide-in-from-right {
    transition: -webkit-transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
    -webkit-transform: translateX(143%);
            transform: translateX(143%);
}

.visible {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}

.about-me h1 {
    font-size: 24px;
}

.about-me p {
    font-size: 16px;
    line-height: 1.5;
    white-space: break-spaces;
}

.about-me-image {

    width: 400px;
    height: 400px;
    margin-top: 140px;
}
header {
    height: 60px;
    display: flex;
    margin: 0px 180px;
    align-items: center;
    padding-top: 10px;
}

.logo {
    flex: 1 1;
    font-size: 15px;
    text-align: left;
    padding: 0 40px;
}

.nav-bar {
    text-align: right;
    padding: 0 40px;
}

.nav-bar ul {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 40px;
    grid-gap: 10px;
    gap: 10px;
}

.nav-bar li {
    justify-content: space-between;
}

.nav-bar a {
    text-decoration: none;
    color: rgb(211, 213, 120);
}

.nav-bar a:hover {
    color: rgb(109, 87, 199);
}
.projects {
    justify-content: center;
    margin: 0px 220px;
}

.card-container {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 30px;
    gap: 30px;
}

.card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 300px;
    height: 200px;
    background-color: rgb(104, 143, 211);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    cursor: pointer;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
    padding: 10px 10px;
    white-space: pre-wrap;

}

.card:hover {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
}

.card__title {
    font-size: 14px;
    font-weight: bold;
    text-align: center;
    margin: 16px 0;
}

.card__description {
    font-size: 13px;
    margin: 0 16px;
    padding-bottom: 60px;
    text-align: center;
}

.card__tools {
    font-size: 10px;
    text-align: left;
}

.card__tools li {
    margin: 0 15px;
}

.card__tools ul {
    padding-left: 15px;
    text-align: center;
    display: flex;
    list-style: none;
}
.copyright {
    font-size: 12px;
    font-weight: bold;
    text-align: left;
}

.social-buttons {
    text-align: right;
}

.social-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-gap: 5px;
    gap: 5px;
}

.container {
    margin: 0 220px;
    justify-content: space-between;
    align-items: flex-end;
    display: flex;
    padding-bottom: 10px;
}
.contact {
    margin: 80px 220px;
}

.contact-container {

    padding: 20px;
    background-color: rgb(104, 143, 211);
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    display: flex;
    width: 350px;
    grid-gap: 290px;
    gap: 290px;
}

.form-input {
    margin-left: 20px;
}

.form-input input {
    justify-content: space-between;
    display: block;
    margin-top: 10px;
    width: 100%;
    box-sizing: border-box;
    border: 2px solid #6c63ff;
    padding: 0.8rem 1rem;
    border-radius: 7px;
    margin-bottom: 0.5rem;
    transition: 0.3s;
}

.form-input textarea {
    display: block;
    width: 100%;
    height: 100px;
    resize: none;
    margin-top: 10px;
    box-sizing: border-box;
    border: 2px solid #6c63ff;
    padding: 0.8rem 1rem;
    border-radius: 7px;
    margin-bottom: 0.5rem;
    transition: 0.3s;
}

.contact-container button {
    cursor: pointer;
    border-radius: 5px;
    padding: 0.5rem 1.7rem;
    border: none;
    -webkit-user-select: none;
            user-select: none;
    color: #fff;
    background: #0074d9;
    margin-top: 10px;
}

.contact-container button:hover {
    background: #0063b1;
}
